/*

https://github.com/xiangyuecn/Recorder
*/
(function(window){
"use strict";

//
window.RecorderLM="2019-01-06 21:38:00";
var NOOP=function(){};
//end  ****copywavmp3*****

function Recorder(set){
	return new initFn(set);
};
//
Recorder.IsOpen=function(){
	var stream=Recorder.Stream;
	if(stream){
		var tracks=stream.getTracks();
		if(tracks.length>0){
			return tracks[0].readyState=="live";
		};
	};
	return false;
};
//
Recorder.Support=function(){
	var AC=window.AudioContext;
	if(!AC){
		AC=window.webkitAudioContext;
	};
	if(!AC){
		return false;
	};
	var scope=navigator.mediaDevices||{};
	if(!scope.getUserMedia){
		scope=navigator;
		scope.getUserMedia||(scope.getUserMedia=scope.webkitGetUserMedia||scope.mozGetUserMedia||scope.msGetUserMedia);
	};
	if(!scope.getUserMedia){
		return false;
	};
	
	Recorder.Scope=scope;
	if(!Recorder.Ctx||Recorder.Ctx.state=="closed"){
		//number of hardware contexts reached maximum (6)
		Recorder.Ctx=new AC();
	};
	return true;
};
function initFn(set){
	var o={
		type:"mp3" //mp3,wavwavmp3jsmp3js
		,bitRate:16 // wav:168MP38kbps 1k/s8kbps 2k/s 
		
		,sampleRate:16000 //wav=sampleRate*mp3
					//wavmp348000, 44100, 32000, 24000, 22050, 16000, 12000, 11025, 8000
					//https://www.cnblogs.com/devin87/p/mp3-recorder.html
		
		,bufferSize:4096//AudioContextonProcessAudioContext.sampleRate=48000409612/s
				//256, 512, 1024, 2048, 4096, 8192, or 16384
				//2048X5
		,onProcess:NOOP //fn(this.buffer,powerLevel,bufferDuration,bufferSampleRate) buffer=[PCM,...]powerLevel0-100bufferDurationbufferSampleRate
	};
	
	for(var k in set){
		o[k]=set[k];
	};
	this.set=o;
};
Recorder.prototype=initFn.prototype={
	//True(),False(msg,isUserNotAllow)close
	open:function(True,False){
		True=True||NOOP;
		False=False||NOOP;
		
		if(Recorder.IsOpen()){
			True();
			return;
		};
		if(!Recorder.Support()){
			False("",false);
			return;
		};
		
		var f1=function(stream){
			Recorder.Stream=stream;
			
			//https://github.com/xiangyuecn/Recorder/issues/14 track.readyState!="live"
			setTimeout(function(){
				if(Recorder.IsOpen()){
					True();
				}else{
					False("");
				};
			},100);
		};
		var f2=function(e){
			var code=e.name||e.message||"";
			console.error(e);
			var notAllow=/Permission|Allow/i.test(code);
			False(notAllow?"":""+code,notAllow);
		};
		var pro=Recorder.Scope.getUserMedia({audio:true},f1,f2);
		if(pro&&pro.then){
			pro.then(f1)["catch"](f2);
		};
	}
	//
	,close:function(call){
		call=call||NOOP;
		
		var This=this;
		This._stop();
		
		var stream=Recorder.Stream;
		if(stream){
			var tracks=stream.getTracks();
			for(var i=0;i<tracks.length;i++){
				tracks[i].stop();
			};
		};
		
		Recorder.Stream=0;
		call();
	}
	
	
	//openstop
	,start:function(){
		var This=this,ctx=Recorder.Ctx;
		var buffer=This.buffer=[];//
		This.recSize=0;//
		This._stop();
		
		This.state=0;
		if(!Recorder.IsOpen()){
			return;
		};
		console.log("["+Date.now()+"]Start");
		This.srcSampleRate=ctx.sampleRate;
		This.isMock=0;
		
		if(ctx.state=="suspended"){
			ctx.resume().then(function(){
				console.log("ctx resume");
				This._start();
			});
		}else{
			This._start();
		};
	}
	,_start:function(){
		var This=this,set=This.set,buffer=This.buffer;
		var ctx=Recorder.Ctx;
		var media=This.media=ctx.createMediaStreamSource(Recorder.Stream);
		var process=This.process=(ctx.createScriptProcessor||ctx.createJavaScriptNode).call(ctx,set.bufferSize,1,1);//
		
		var onInt;
		process.onaudioprocess=function(e){
			if(This.state!=1){
				return;
			};
			var o=e.inputBuffer.getChannelData(0);//
			var size=o.length;
			This.recSize+=size;
			
			var res=new Int16Array(size);
			var power=0;
			for(var j=0;j<size;j++){//floatTo16BitPCM 
				//var s=Math.max(-1,Math.min(1,o[j]*8));//PCM 8
				var s=Math.max(-1,Math.min(1,o[j]));
				s=s<0?s*0x8000:s*0x7FFF;
				res[j]=s;
				power+=Math.abs(s);
			};
			buffer.push(res);
			
			/*https://blog.csdn.net/jody1989/article/details/73480259
			:
				10000
					
						power/10000*100 
					
						(1+Math.log10(power/10000))*100
			*/
			power/=size;
			var powerLevel;
			if(power<1251){//125010%
				powerLevel=Math.round(power/1250*10);
			}else{
				powerLevel=Math.round(Math.min(100,Math.max(0,(1+Math.log10(power/10000))*100)));
			}
			
			var bufferSampleRate=This.srcSampleRate;
			var duration=Math.round(This.recSize/bufferSampleRate*1000);
			
			clearTimeout(onInt);
			onInt=setTimeout(function(){
				set.onProcess(buffer,powerLevel,duration,bufferSampleRate);
			});
		};
		
		media.connect(process);
		process.connect(ctx.destination);
		This.state=1;
	}
	,_stop:function(){
		var This=this;
		if(This.state){
			This.state=0;
			This.media.disconnect();
			This.process.disconnect();
		};
	}
	/**/
	,pause:function(_resume){
		var This=this;
		if(This.state){
			This.state=_resume||2;
		};
	}
	/**/
	,resume:function(){
		this.pause(1);
	}
	/*stoppcm[1,2,3...]pcm*/
	,mock:function(pcmData,pcmSampleRate){
		var This=this;
		This.isMock=1;
		This.buffer=[pcmData];
		This.recSize=pcmData.length;
		This.srcSampleRate=pcmSampleRate;
		return This;
	}
	/*
	blob
		True(blob,duration) blobaudio/mp3|wav
							duration
		False(msg)
	*/
	,stop:function(True,False){
		console.log("["+Date.now()+"]Stop");
		True=True||NOOP;
		False=False||NOOP;
		var This=this,set=This.set;
		
		if(!This.isMock){
			if(!This.state){
				False("");
				return;
			};
			This._stop();
		};
		var size=This.recSize;
		if(!size){
			False("");
			return;
		};
		
		var sampleRate=set.sampleRate
			,srcSampleRate=This.srcSampleRate;
		// https://www.cnblogs.com/blqw/p/3782420.html
		var step=srcSampleRate/sampleRate;
		if(step>1){//
			size=Math.floor(size/step);
		}else{
			step=1;
			sampleRate=srcSampleRate;
			set.sampleRate=sampleRate;
		};
		//
		var res=new Int16Array(size);
		var last=0,idx=0;
		for (var n=0,nl=This.buffer.length;n<nl;n++) {
			var o=This.buffer[n];
			var i=last,il=o.length;
			while(i<il){
				res[idx]=o[Math.round(i)];
				idx++;
				i+=step;//
			};
			last=i-il;
		};
		var duration=Math.round(size/sampleRate*1000);
		
		setTimeout(function(){
			debugger
			var t1=Date.now();
			console.log(set.type);
			console.log(This);
			console.log(This[set.type]);
			This[set.type](res,function(blob){
				console.log(res);
				console.log(blob);
				console.log("["+Date.now()+"]End",blob,duration,":"+(Date.now()-t1));
				True(blob,duration);
			},function(msg){
				False(msg);
			});
		});
	}
//end ****copywavmp3*****




};

window.Recorder=Recorder;

})(window);