<template>
  <div>
    <audio id="audio"></audio>
    <div class="iconfont icon-sound"></div>
    <el-progress :show-text="false" :stroke-width="5" :percentage="Recordingtage" color="#2E70ED"></el-progress>
    <div class="time-text">{{Recordingtime}}</div>
    <button @click="startRecording" v-if="!bengin">begin</button>
    <button @click="endRecording" v-if="bengin"></button>
    <ns-Video></ns-Video>
  </div>
</template>

<script>
import "@/assets/recorderjs/recorder-core.js";
import "@/assets/recorderjs/waveview.js";
import "@/assets/recorderjs/jquery.js";
import "@/assets/recorderjs/mp3-engine.js";
import "@/assets/recorderjs/mp3.js";

export default {
  name: "recorderDemo",
  data() {
    return {
      bengin: false,
      Recordingtime: "00",
      Recordingtage: "30",
      Recordingnum: "",
      rec: ''
    };
  },
  created() {},
  mounted(){},
  methods: {
    startRecording() {
      this.bengin = true;
      this.recopen();
    },
    endRecording() {
      this.bengin = false;
      this.recstop();
    },
    recopen(){  //
      let _this = this;
      let type = "mp3"; // 
      let bit = 16; // kbps mp3wav816
      let sample = 16000; // mp3wav
      this.rec=window.Recorder({
        type:type,
        bitRate:bit,
        sampleRate:sample,
        onProcess:function(buffers,level,time,sampleRate){
          console.log('1', buffers);
          console.log('1', level);
          console.log('1', time);
          console.log('1', sampleRate);
        }
      });
      this.rec.open(
        function(){
          console.log(":"+type+" "+bit+"kbps");
          setTimeout(function(){  //
            _this.recstart();
          },200)
        },
        function(e,isUserNotAllow){
          console.log((isUserNotAllow?"UserNotAllow":"")+""+e);
        }
      );
    },
    recstop(batCall){
      let _this = this;
      if(this.rec){
        if(!batCall){
          console.log(""+this.rec.set.type+"...");
        }
        this.rec.stop(
        function(blob,time){
          console.log('blob:=======', blob);
          console.log('time:=======', time);
          this.down(blob, new Date().getTime(), 'mp3')
        },function(s){
          console.log(""+s);
          batCall&&batCall();
        });
      }
      setTimeout(function(){
        _this.recclose()
      },200)
    },
    recstart() {
      if (this.rec) {
        this.rec.start();
        console.log("...");
      }
    },
    recclose() { // 
      if (this.rec) {
        this.rec.close(function() {
          console.log("");// 
        });
      }
    },
    down(res, name, type){
      let blob = new Blob([res], {
        type: 'application/vnd.openxmlformats-officedocument.wordprocessingml.document'
      });
      let downloadElement = document.createElement('a');
      let href = window.URL.createObjectURL(blob);
      downloadElement.href = href;
      downloadElement.download = decodeURI(name) + '.' + type;
      document.body.appendChild(downloadElement);
      downloadElement.click();
      document.body.removeChild(downloadElement);
      window.URL.revokeObjectURL(href);
    },
  }
};
</script>


