/*
mp3mp3-engine.js
https://github.com/xiangyuecn/Recorder

mp3wav

https://developer.mozilla.org/en-US/docs/Web/HTML/Supported_media_formats
*/
(function(){
"use strict";

Recorder.prototype.enc_mp3={
	stable:true
	,testmsg:"48000, 44100, 32000, 24000, 22050, 16000, 12000, 11025, 8000"
};


Recorder.prototype.mp3=function(res,True,False){
		var This=this,set=This.set,size=res.length;
		//https://github.com/wangpengfei15975/recorder.js
		//https://github.com/zhuker/lamejs bug:8kfixhttps://github.com/zhuker/lamejs/pull/11
		var mp3=new Recorder.lamejs.Mp3Encoder(1,set.sampleRate,set.bitRate);
		
		var blockSize=57600;
		var data=[];
		
		var idx=0;
		var run=function(){
			if(idx<size){
				var buf=mp3.encodeBuffer(res.subarray(idx,idx+blockSize));
				if(buf.length>0){
					data.push(buf);
				};
				idx+=blockSize;
				setTimeout(run);//Worker? 
			}else{
				var buf=mp3.flush();
				if(buf.length>0){
					data.push(buf);
				};
				
				True(new Blob(data,{type:"audio/mp3"}));
			};
		};
		run();
	}
	
})();